import React from "react";

import { Wrapper, Content, Logo, NavigationBar, Button } from "./Header.styles";
import NavigationButton from "../NavigationButton";

const Header = ({ pageName }) => {
  const callback = (e) => {};

  return (
    <Wrapper>
      <Content>
        <Logo>
          <span class="material-symbols-outlined">format_paint</span>
          <h2>Pixel Art Converter</h2>
        </Logo>

        <NavigationBar>
          <NavigationButton
            text="Home"
            selected={pageName === "Home"}
            callback={callback}
            icon_name="Home"
            url="/"
          />
          <NavigationButton
            text="Converter"
            selected={pageName === "Converter"}
            callback={callback}
            icon_name="Brush"
            url="/converter"
          />
          <NavigationButton
            text="Gallery"
            selected={pageName === "Gallery"}
            callback={callback}
            icon_name="photo_library"
            url="/gallery"
          />
        </NavigationBar>
      </Content>
    </Wrapper>
  );
};

export default Header;
