import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  align-items: center;
  padding: 30px 5px;
  flex-direction: column;
  margin: 0 auto;
  max-width: var(--maxWidth);
  margin-bottom: 35px;

  h1 {
    color: black;
    padding: 0;
    margin: 0;
  }
`;

export const Panel = styled.div`
  max-width: var(--maxWidth);
  background-color: var(--darkGrey);
  width: 95vw;
  height: 75vh;
  position: relative;
  border-radius: 20px;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.2),
    0 6px 20px 5px rgba(0, 0, 0, 0.19);
`;

export const OccludingContainer = styled.div`
  position: absolute;
  width: ${(props) => props.width}%;
  height: 100%;
  overflow: hidden;
`;

export const Container = styled.div`
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 95vw;
  height: 100%;
  max-width: var(--maxWidth);
`;
export const Image = styled.img`
  border-radius: 20px;
  max-width: 100%;
  max-height: 96%;
  src: ${(props) => props.src};
`;

export const Slider = styled.input`
  width: 100%;
  height: 100%;
  position: absolute;
  outline: none;
  background-color: transparent;
  margin: 0;

  cursor: pointer;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  transition: 0.25s all ease-in-out;
  -moz-transition: 0.25s all ease-in-out;
  -webkit-transition: 0.25s all ease-in-out;

  &::-webkit-slider-runnable-track {
    width: 100%;
    height: ${(props) => (props.value == 0 || props.value == 100 ? 95 : 100)}%;
    background: transparent;
  }

  &::-webkit-slider-thumb {
    width: 6px;
    height: 100%;
    background-color: white;
    cursor: pointer;
    -webkit-appearance: none;
    border-radius: 20px;
  }
  &::-moz-range-thumb {
    width: 6px;
    height: ${(props) => (props.value == 0 || props.value == 100 ? 95 : 100)}%;
    background-color: white;
    cursor: pointer;
    border: none;
    border-radius: 20px;
  }
`;

export const SliderButton = styled.div`
  width: 31px;
  height: 31px;
  border-radius: 50%;
  background-color: var(--white);
  position: absolute;
  top: 50%;
  user-select: none;
  left: calc(
    ${(props) => (-(props.pos - 50) / 50) * 3}px + ${(props) => props.pos}%
  );
  /* This aligns the button with the slider thumb. Slider thumb deviates from expected position in a linear fashion
  as it approaches the boundaries. This code compensates for that.*/
  transform: translate(-50%, -50%);

  &::after {
    content: "";
    padding: 4px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.7);
    border-width: 3px 3px 0 0;
    border-radius: 15%;
    top: 50%;
    right: 4px;
    transform: translate(0, -50%) rotate(45deg);
    position: absolute;
  }

  &::before {
    content: "";
    padding: 4px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.7);
    border-width: 0 0 3px 3px;
    border-radius: 15%;
    top: 50%;
    left: 4px;
    transform: translate(0, -50%) rotate(45deg);
    position: absolute;
  }
`;

export const NextButton = styled.div`
  cursor: pointer;
  position: absolute;
  padding: 10px 10px 5px 10px;
  margin: 0;
  background-color: rgba(0, 0, 0, 0);
  top: 50%;
  transform: translate(0%, -50%);
  user-select: none;
  right: 0px;

  &:hover {
    background-color: rgba(0, 0, 0, 0.4);
    .material-symbols-outlined {
      color: rgba(255, 255, 255, 1);
    }
  }

  .material-symbols-outlined {
    color: rgba(255, 255, 255, 0.3);
    font-size: 30px;
    padding: 0;
    margin: 0;

    &:hover {
      color: rgba(255, 255, 255, 1);
    }
  }
`;

export const PrevButton = styled.div`
  cursor: pointer;
  position: absolute;
  padding: 10px 10px 5px 10px;
  background-color: rgba(0, 0, 0, 0);
  top: 50%;
  transform: translate(0%, -50%);
  user-select: none;
  left: 0px;

  &:hover {
    background-color: rgba(0, 0, 0, 0.4);
    .material-symbols-outlined {
      color: rgba(255, 255, 255, 1);
    }
  }

  .material-symbols-outlined {
    font-size: 30px;
    color: rgba(255, 255, 255, 0.3);
  }
`;

export const SlideNumberPanel = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
  padding: 3px;
  color: var(--lightGrey);
  border-radius: 20px;
  background-color: var(--darkGrey);

  span {
    font-size: 14px;
  }
`;
export const ImageDotWrapper = styled.div`
  margin: 10px 0;
  display: flex;
  gap: 5px;
`;
export const Dot = styled.button`
  border: none;
  outline: none;
  cursor: pointer;
  background-color: var(--darkGrey);
  opacity: ${(props) => (props.selected ? 1 : 0.6)};
  width: 15px;
  height: 15px;
  border-radius: 50%;
  transition: all 0.2s;

  &:hover {
    opacity: 1;
  }
`;
export const FadeContainer = styled.div`
  animation: fadeIn 0.5s ease-in-out;
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
`;
