import React from "react";
// Routing
import { Link } from "react-router-dom";
// Components
import {
  Wrapper,
  StepBanner,
  StepDescription,
  GetStartedButton,
} from "./StepsSection.styles";
// Images
import step_0 from "../../images/steps/step_0.jpg";

const StepsSection = () => {
  return (
    <Wrapper>
      <Step stepNum="1" description="Select an image" img={step_0} />

      <Step
        stepNum="2"
        description="Select pixel size and pre-processing parameters"
        img={step_0}
      />

      <Step stepNum="3" description="Select a color palette" img={step_0} />

      <Step stepNum="4" description="Select dithering method" img={step_0} />

      <Step stepNum="5" description="Share your results" />

      <ConverterLink />
    </Wrapper>
  );
};

const Step = ({ stepNum, description, img }) => (
  <>
    <StepBanner>
      <h2>Step {stepNum}</h2>
    </StepBanner>
    <StepDescription>
      <h2>{description}</h2>
      {img ? <img src={img} /> : null}
    </StepDescription>
  </>
);

const ConverterLink = () => (
  <Link to="/converter" style={{ textDecoration: "none" }}>
    <GetStartedButton>
      <span class="material-symbols-outlined">Brush</span>
      <span>Get started</span>
    </GetStartedButton>
  </Link>
);

export default StepsSection;
