import React from "react";

import {
  Wrapper,
  SettingsTab,
  Slider,
  TextInput,
  SettingsTabExtension,
  Grouper,
} from "./ParametersPanel.styles";

//TO DO: create a custom hook to manage state
const ParametersPanel = ({
  pixelSize,
  ditheringIntensity,
  setPixelSize,
  setDitheringIntensity,
  setDitheringMethod,
  noiseReductionLevel,
  setNoiseReductionLevel,
  enableEdges,
  setEnableEdges,
  edgeSensitivity,
  setEdgeSensitivity,
}) => {
  return (
    <Wrapper>
      <Grouper>
        <PixelSizeTab pixelSize={pixelSize} setPixelSize={setPixelSize} />
        <NoiseReductionTab
          noiseReductionLevel={noiseReductionLevel}
          setNoiseReductionLevel={setNoiseReductionLevel}
        />
      </Grouper>
      <DitheringIntensityTab
        ditheringIntensity={ditheringIntensity}
        setDitheringIntensity={setDitheringIntensity}
        setDitheringMethod={setDitheringMethod}
      />
      <EdgeDetectionTab
        enableEdges={enableEdges}
        setEnableEdges={setEnableEdges}
        edgeSensitivity={edgeSensitivity}
        setEdgeSensitivity={setEdgeSensitivity}
      />
    </Wrapper>
  );
};

export default ParametersPanel;

const PixelSizeTab = ({ pixelSize, setPixelSize }) => {
  const onChange = (e) => {
    if (e.target.value.includes(".") || isNaN(e.target.value)) return;
    setPixelSize(Math.max(Math.min(e.target.value, 100), 0));
  };

  return (
    <SettingsTab>
      <ValueSlider text="Pixel size:" value={pixelSize} onChange={onChange} />
      <TextInputBox value={pixelSize} onChange={onChange} />
    </SettingsTab>
  );
};

const DitheringIntensityTab = ({
  ditheringIntensity,
  setDitheringIntensity,
  setDitheringMethod,
}) => {
  const onChange = (e) => {
    if (e.target.value.includes(".") || isNaN(e.target.value)) return;
    setDitheringIntensity(Math.max(Math.min(e.target.value, 100), 0));
  };

  return (
    <SettingsTab>
      <ValueSlider
        text="Dithering intensity:"
        value={ditheringIntensity}
        onChange={onChange}
      />
      <TextInputBox value={ditheringIntensity} onChange={onChange} />
      <SettingsTabExtension>
        <label>Method:</label>
        <select onChange={(e) => setDitheringMethod(e.target.value)}>
          <option selected="true" value="Knoll">
            Method 1
          </option>
          <option value="Ordered">Method 2</option>
        </select>
      </SettingsTabExtension>
    </SettingsTab>
  );
};

const NoiseReductionTab = ({ noiseReductionLevel, setNoiseReductionLevel }) => {
  const onChange = (e) => {
    if (e.target.value.includes(".") || isNaN(e.target.value)) return;
    setNoiseReductionLevel(Math.max(Math.min(e.target.value, 100), 0));
  };

  return (
    <SettingsTab>
      <ValueSlider
        text="Noise reduction:"
        value={noiseReductionLevel}
        onChange={onChange}
      />
      <TextInputBox value={noiseReductionLevel} onChange={onChange} />
    </SettingsTab>
  );
};

const EdgeDetectionTab = ({
  enableEdges,
  setEnableEdges,
  edgeSensitivity,
  setEdgeSensitivity,
}) => {
  const onChange = (e) => {
    if (e.target.value.includes(".") || isNaN(e.target.value)) return;
    setEdgeSensitivity(Math.max(Math.min(e.target.value, 100), 0));
  };
  return (
    <SettingsTab>
      <input
        type="checkbox"
        checked={enableEdges}
        style={{ width: "20px", margin: "0", borderRadius: "20px" }}
        id="enableEdges"
        onChange={(e) => setEnableEdges(e.target.checked)}
      ></input>
      <label for="enableEdges">Enable edge detection</label>

      <SettingsTabExtension>
        <ValueSlider
          text="Edge sensitivity:"
          value={edgeSensitivity}
          onChange={onChange}
        />
        <TextInputBox value={edgeSensitivity} onChange={onChange} />
      </SettingsTabExtension>
    </SettingsTab>
  );
};

const ValueSlider = ({ text, value, low, high, onChange }) => {
  return (
    <>
      <h2>{text}</h2>
      <Slider type="range" value={value} onChange={onChange} />
    </>
  );
};

const TextInputBox = ({ value, onChange }) => {
  return (
    <span>
      <TextInput type="text" value={value} onChange={onChange} /> %
    </span>
  );
};
