import styled from "styled-components";

export const UploadBoxContainer = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 15px;
  background-color: var(--medGrey);
  border-radius: 15px;
  padding: 25px;
  max-width: 550px;
  width: 100%;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.2),
    0 6px 20px 5px rgba(0, 0, 0, 0.19);

  h2 {
    margin: 0;
    font-size: 1.4rem;
    opacity: 0.8;
    font-weight: 300;
  }
`;

export const UploadBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-radius: 15px;
  border-color: rgba(90, 87, 87, 0.6);
  border-style: dashed;
  width: 100%;
  height: 350px;
  text-align: center;
  padding: 50px;
  gap: 15px;

  border: ${(props) =>
    props.is_dragging
      ? "medium solid #00adb5"
      : "medium dashed rgba(90, 87, 87, 0.6)"};

  h2 {
  }
  .material-symbols-rounded {
    color: var(--white);
    opacity: 0.8;
    font-size: 5.5rem;
  }
`;

export const UploadButton = styled.label`
  background-color: #00adb5;
  border: none;
  cursor: pointer;
  border-radius: 15px;
  padding: 6px 9px;
  font-size: 1.4rem;
  font-weight: bold;
  color: var(--darkGrey);
  transition: opacity 0.5s ease-in-out;
  &:hover {
    background-color: rgba(0, 173, 181, 0.5);
  }
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
`;

export const UploadDetailsContainer = styled.div`
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
  width: 100%;
  max-width: 550px;
  padding: 15px;
  background-color: var(--medGrey);
  border-radius: 15px;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.2),
    0 6px 20px 5px rgba(0, 0, 0, 0.19);
  gap: 10px;

  h2 {
    font-size: 1.6rem;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
  img {
    display: block;
    max-width: 100%;
    max-height: 380px;
    margin: auto;
  }
`;
