import React from "react";
import {
  CanvasContainer,
  Spinner,
  SpinnerContainer,
} from "./OutputCanvas.styles";

const OutputCanvas = ({ canvasRef, loading, loadingStage }) => {
  return (
    <CanvasContainer>
      <canvas
        ref={canvasRef}
        style={{ maxHeight: "100%", maxWidth: "100%", objectFit: "contain" }}
      />
      {loading && (
        <SpinnerContainer>
          <Spinner />
          {loadingStage}
        </SpinnerContainer>
      )}
    </CanvasContainer>
  );
};

export default OutputCanvas;
