import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  useMemo,
} from "react";
import _ from "underscore";
import { render } from "../renderer.js";
import {
  setupBilateralFilterShadingProgram,
  bilateralSmoothing,
} from "../bilateralShader.js";
import { getIntermediateFramebuffer } from "../intermediateFramebuffer.js";

import {
  setupPixelartShadingProgram,
  renderPixelart,
  PixelartConfig,
} from "../pixelartShader.js";
// Styling
import styled from "styled-components";
// Components
import Header from "./Header";
import UploadPanel from "./UploadPanel";
import ParametersPanel from "./ParametersPanel";
import PaletteSettingsPanel from "./PaletteSettingsPanel";
import OutputCanvas from "./OutputCanvas";

// Color palettes
import palettes from "../colorPalettes/palettes.json";

function checkSync(gl, sync) {
  // Check the sync status with a timeout of 0 to avoid blocking
  var waitResult = gl.clientWaitSync(sync, 0, 0);
  if (waitResult === gl.TIMEOUT_EXPIRED) {
    return false;
  } else if (waitResult === gl.WAIT_FAILED) {
    return false;
  } else {
    return true;
  }
}

const Converter = () => {
  const [img, setImg] = useState(null);
  const [pixelSize, setPixelSize] = useState(25);
  const [ditheringIntensity, setDitheringIntensity] = useState(50);
  const [ditheringMethod, setDitheringMethod] = useState("Knoll");
  const [autoPalette, setAutoPalette] = useState(true);
  const [autoPaletteSize, setAutoPaletteSize] = useState(16);
  const [autoGeneratedPalettes, setAutoGeneratedPalettes] = useState([]);
  const [selectedCustomPalette, setSelectedCustomPalette] = useState(0);
  const [noiseReductionLevel, setNoiseReductionLevel] = useState(25);
  const [enableEdges, setEnableEdges] = useState(false);
  const [edgeSensitivity, setEdgeSensitivity] = useState(25);

  const [imgFile, setImgFile] = useState(null);

  const [loading, setLoading] = useState(false);
  const [loadingStage, setLoadingStage] = useState("");
  const worker = useRef(new Worker("offscreencanvas.js"));
  const canvas = useRef(null);
  const osCanvas = useRef(null);
  const image = useRef(null);

  const gl = useRef(null);
  const intermediateBuffer = useRef(null);
  const bilateralShadingData = useRef(null);
  const knollPixelartShadingData = useRef(null);
  const orderedPixelartShadingData = useRef(null);

  const cpaTimeoutId = useRef(0);
  const siTimeoutId = useRef(0);

  const debouncedCreatePixelArt = () => {
    createPixelArt();
  };

  const debouncedSmoothImage = (iterations, callback) => {
    smoothImage(iterations, callback);
  };

  // Sends a message to the webworker to generate color palette for the imageData
  const generateColorPalettes = (imageData) => {
    setLoading(true);
    setLoadingStage("Generating color palettes");
    worker.current.postMessage({
      job: "generateColorPalettes",
      maxPaletteSize: 256,
      imageData: imageData,
    });
  };

  const createPixelArt = () => {
    setLoadingStage("Rendering image");

    const shadingData =
      ditheringMethod === "Knoll"
        ? knollPixelartShadingData.current
        : orderedPixelartShadingData.current;
    const palette = autoPalette
      ? autoGeneratedPalettes[Math.log2(autoPaletteSize) - 1]
      : palettes[selectedCustomPalette]["colors"];

    renderPixelart(
      gl.current,
      shadingData,
      new PixelartConfig(
        pixelSize,
        ditheringMethod,
        ditheringIntensity / 100,
        palette
      ),
      intermediateBuffer.current[1]
    );

    pollRenderStatus(() => {
      setLoadingStage("");
      setLoading(false);
    });
  };
  // It's really not a full render. It smoothes the image so that color palettes can be generated
  const smoothImage = (iterations, callback) => {
    setLoading(true);
    setLoadingStage("Pre-processing image");

    bilateralSmoothing(
      gl.current,
      bilateralShadingData.current,
      image.current,
      intermediateBuffer.current[0],
      intermediateBuffer.current[1],
      iterations
    );
    // When done rendering load the image in imageData and call the
    callback();
  };

  // Synchronizes with webGl to know when rendering is done so that we can read the image and calls callback when done
  const pollRenderStatus = (callback = () => {}) => {
    const syncObj = gl.current.fenceSync(
      gl.current.SYNC_GPU_COMMANDS_COMPLETE,
      0
    );

    const poll = () => {
      if (checkSync(gl.current, syncObj)) {
        // Get rid of sync obj
        gl.current.deleteSync(syncObj);
        callback();
      } else {
        setLoading(true);
        setTimeout(poll, 100);
      }
    };

    setTimeout(poll, 100);
  };

  // Defines the event handler to respond to messages received by the webworker
  useEffect(() => {
    //ctx.current = canvas.current.getContext("2d");
    worker.current.onmessage = (e) => {
      if (e.data instanceof Array) {
        setAutoGeneratedPalettes(e.data);
      } else {
        // Update on loading status
        setLoadingStage(e.data.stage);
      }
    };
    // Get webgl2 context and compile shaders
    if (canvas.current) {
      gl.current = canvas.current.getContext("webgl2", {
        preserveDrawingBuffer: true,
      });
      bilateralShadingData.current = setupBilateralFilterShadingProgram(
        gl.current
      );
      knollPixelartShadingData.current = setupPixelartShadingProgram(
        gl.current,
        true
      );
      orderedPixelartShadingData.current = setupPixelartShadingProgram(
        gl.current,
        false
      );
      intermediateBuffer.current = getIntermediateFramebuffer(gl.current);

      osCanvas.current = document.createElement("canvas");
    }
  }, []);

  // Once an image is uploaded this will set up the canvas and will start outputing results
  useEffect(() => {
    if (img) {
      setAutoGeneratedPalettes([]);
      image.current = new Image();
      image.current.onload = () => {
        canvas.current.width = image.current.naturalWidth;
        canvas.current.height = image.current.naturalHeight;

        osCanvas.current.width = image.current.naturalWidth;
        osCanvas.current.height = image.current.naturalHeight;
        const osContext = osCanvas.current.getContext("2d");
        osContext.drawImage(image.current, 0, 0);

        generateColorPalettes(
          osContext.getImageData(
            0,
            0,
            osCanvas.current.width,
            osCanvas.current.height
          )
        );
      };
      image.current.src = img;
    }
  }, [img]);

  // When parameters change re-render pixelArt
  useEffect(() => {
    if (!image.current) return;
    if (autoGeneratedPalettes.length == 0 && autoPalette) return;

    debouncedCreatePixelArt();
  }, [
    pixelSize,
    ditheringIntensity,
    ditheringMethod,
    autoPalette,
    autoPaletteSize,
    selectedCustomPalette,

    enableEdges,
    edgeSensitivity,
  ]);

  // When parameters change smooth then create pixel art
  useEffect(() => {
    if (!image.current) return;
    if (autoGeneratedPalettes.length == 0 && autoPalette) return;

    debouncedSmoothImage((noiseReductionLevel * 4) / 100, createPixelArt);
  }, [noiseReductionLevel, autoGeneratedPalettes]);

  return (
    <>
      <Header pageName="Converter" />
      <Wrapper>
        <UploadPanel img={img} setImg={setImg} setImgFile={setImgFile} />
        <ParametersPanel
          pixelSize={pixelSize}
          ditheringIntensity={ditheringIntensity}
          setPixelSize={setPixelSize}
          setDitheringIntensity={setDitheringIntensity}
          setDitheringMethod={setDitheringMethod}
          noiseReductionLevel={noiseReductionLevel}
          setNoiseReductionLevel={setNoiseReductionLevel}
          enableEdges={enableEdges}
          setEnableEdges={setEnableEdges}
          edgeSensitivity={edgeSensitivity}
          setEdgeSensitivity={setEdgeSensitivity}
        />
        <MainWrapper>
          <PaletteSettingsPanel
            autoPalette={autoPalette}
            autoPaletteSize={autoPaletteSize}
            autoGeneratedPalettes={autoGeneratedPalettes}
            selectedCustomPalette={selectedCustomPalette}
            setAutoPalette={setAutoPalette}
            setAutoPaletteSize={setAutoPaletteSize}
            setSelectedCustomPalette={setSelectedCustomPalette}
            loading={loading}
            loadingStage={loadingStage}
          />
          <OutputCanvas
            canvasRef={canvas}
            loading={loading}
            loadingStage={loadingStage}
          />
        </MainWrapper>
      </Wrapper>
    </>
  );
};

export default Converter;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  margin: auto auto;

  padding: 40px 10px 5px 10px;
  gap: 10px;
`;

const MainWrapper = styled.div`
  display: flex;
  width: 100%;
  gap: 15px;

  @media screen and (max-width: 1150px) {
    flex-direction: column;
    align-items: center;
  }
`;
