import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;

  width: 30%;
  min-width: 350px;
  max-width: 550px;
  gap: 8px;

  @media screen and (max-width: 1150px) {
    width: 100%;
  }
`;

export const SettingsTab = styled.div`
  display: flex;
  width: 100%;
  max-width: 550px;
  flex-wrap: wrap;
  gap: 15px;
  padding: 15px;
  background-color: var(--medGrey);
  border-radius: 15px;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.2),
    0 6px 20px 5px rgba(0, 0, 0, 0.19);

  h2 {
    font-size: 1.6rem;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
  span {
    font-size: 1.6rem;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
`;
export const PaletteDropdown = styled.div`
  padding: 15px 5px 5px 5px;
  justify-content: ${(props) => (props.center ? "center" : "none")};
  flex: 1 1 100%;
  border-top: 3px solid #00adb5;
  gap: 15px;
  flex-wrap: wrap;
  display: ${(props) => (props.show ? "flex" : "none")};
  label {
    font-size: 1.6rem;

    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
  select {
    font-size: 1rem;
  }
`;
export const Spinner = styled.div`
  width: 50px;
  height: 50px;
  border: 8px solid #fff;
  border-bottom-color: #00adb5;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`;

export const SpinnerContainer = styled.div`
  display: flex;
  width: 100%;
  gap: 7px;
  background-color: var(--medGrey);
  border-radius: 20px;
  padding: 20px;
  align-items: center;
  flex-direction: column;
  color: white;
  font-size: 1.2rem;
`;

export const CustomPaletteSelectContainer = styled.div`
  flex: 1 1 100%;
  width: 100%;
  max-height: 420px;
  height: 100%;
  overflow-y: scroll;
  border: 2px solid rgba(90, 87, 87, 0.6);
  background-color: var(--darkGrey);
`;

export const SelectedPaletteContainer = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  gap: 8px;
`;
export const SelectedPaletteText = styled.div`
  padding: 3px;
  background-color: var(--darkGrey);
`;
export const SelectedPaletteDisplay = styled.div`
  display: flex;
  flex-wrap: wrap;

  overflow: hidden;
  width: 100%;
  max-width: 410px;
  padding: 5px;
  background-color: var(--darkGrey);
`;

export const Palette = styled.div`
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  margin: 7px 5px;
  gap: 7px;
  padding: 5px;
  background-color: ${(props) =>
    props.selected ? "rgba(90, 87, 87, 0.9)" : "var(--medGrey)"};
  border: 2px solid rgba(90, 87, 87, 0.6);

  &:hover {
    background-color: rgba(90, 87, 87, 0.9);
    cursor: pointer;
  }
`;

export const RadioButtonContainer = styled.label`
  width: 30px;
  height: 30px;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: ${(props) =>
    props.is_checked ? "#00adb5" : "rgba(255, 255, 255, 0.6)"};
  border-radius: 50%;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.1);

  &:hover {
    background-color: ${(props) =>
      props.is_checked ? "#00adb5" : "rgba(255, 255, 255, 0.4)"};
  }

  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    margin: 0;
  }

  span {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    position: absolute;
    background-color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: ${(props) => (props.is_checked ? "block" : "none")};
    box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.1);
  }
`;
