import styled from "styled-components";

export const WrapperSelected = styled.div`
  margin-left: 20px;
  color: var(--white);
  min-width: ${(props) => props.minwidth};
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;

  .UnderlineBar {
    width: calc(100% - 5px);
  }
  h3 {
    font-weight: 800;
  }

  @media screen and (max-width: 720px) {
    margin-bottom: 26px;
    margin-left: 0;
  }
`;
// Fix this later (DRY)
export const WrapperNotSelected = styled.div`
  margin-left: 20px;
  min-width: ${(props) => props.minwidth};
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  opacity: 0.6;

  h3 {
    font-weight: 800;
  }
  &:hover {
    opacity: 1;
    .UnderlineBar {
      width: calc(100% - 5px);
    }
    h3 {
      color: var(--white);
    }
  }

  @media screen and (max-width: 720px) {
    margin-bottom: 20px;
    margin-left: 0;
  }
`;

export const UnderlineBar = styled.div`
  height: 4px;
  background-color: var(--white);
  width: 0px;

  transition: width 0.3s ease;
  border-radius: 10px;
`;

export const TextIconContent = styled.div`
  display: flex;
  .material-symbols-outlined {
    margin-left: -4px;
    padding: 0;
    scale: 0.7;
  }
  padding: 0 4px;
`;

export const IntermediateWrapper = styled.div`
  margin: 0;
  padding: 0;
`;

export const TextIconWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: auto auto;
`;
