import React, { useEffect, useState, useRef, useMemo } from "react";
import {
  Wrapper,
  Panel,
  Image,
  Container,
  Slider,
  OccludingContainer,
  SliderButton,
  NextButton,
  PrevButton,
  Dot,
  ImageDotWrapper,
  FadeContainer,
} from "./DemoPanel.styles";

function importAll(r) {
  return r.keys().map(r);
}

const beforeImages = importAll(
  require.context("../../images/before", false, /\.(png|jpe?g|svg)$/)
);

const afterImages = importAll(
  require.context("../../images/after", false, /\.(png|jpe?g|svg)$/)
);

const numSlides = beforeImages.length;

const DemoPanel = () => {
  // TO DO: Create a hook for this
  const [slideNum, setSlideNum] = useState(0);
  const [sliderVal, setSliderVal] = useState(50);
  const [loaded, setLoaded] = useState(false);

  const imageDots = useMemo(() => {
    return Array.from({ length: numSlides }, (_, i) => (
      <ImageDot
        key={i}
        index={i}
        selected={i === slideNum}
        setSlideNum={setSlideNum}
      />
    ));
  }, [slideNum]);

  useEffect(() => {
    const autoCycle = () => {
      setSlideNum((slideNum + 1) % numSlides);
    };
    const intervalId = setInterval(autoCycle, 5000);

    return () => {
      clearInterval(intervalId);
    };
  }, [slideNum, sliderVal]);

  return (
    <Wrapper>
      <h1>Examples</h1>

      <Panel>
        <FadeContainer key={slideNum}>
          <Container>
            <Image src={afterImages[slideNum]} />
          </Container>

          <OccludingContainer width={sliderVal}>
            <Container>
              <Image src={beforeImages[slideNum]} />
            </Container>
          </OccludingContainer>
        </FadeContainer>
        <SliderButton pos={sliderVal} />
        <Slider
          type="range"
          value={sliderVal}
          onChange={(e) => setSliderVal(e.target.value)}
        />

        <NextButton onClick={() => setSlideNum((slideNum + 1) % numSlides)}>
          <span class="material-symbols-outlined">chevron_right</span>
        </NextButton>

        <PrevButton
          onClick={() => setSlideNum((slideNum - 1 + numSlides) % numSlides)}
        >
          <span class="material-symbols-outlined"> chevron_left</span>
        </PrevButton>
      </Panel>

      <ImageDotWrapper>{imageDots}</ImageDotWrapper>
    </Wrapper>
  );
};

const ImageDot = ({ index, selected, setSlideNum }) => {
  console.log(index);
  return <Dot selected={selected} onClick={() => setSlideNum(index)} />;
};

export default DemoPanel;
