import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;

  gap: 8px;
  width: 100%;
  justify-content: center;
  @media screen and (max-width: 1150px) {
    align-items: center;
    flex-direction: column;
  }
`;
export const Slider = styled.input`
  flex: 1;
  max-width: 50%;

  &::-webkit-slider-runnable-track {
  }
`;
export const Grouper = styled.div`
  display: flex;
  flex: 1 1 100%;
  gap: 8px;
  max-width: 550px;
  width: 100%;
  flex-direction: column;
`;

export const TextInput = styled.input`
  font-size: 1.6rem;
  margin: 0;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 400;
  background-color: var(--darkGrey);
  width: 50px;
  border: none;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.1);
`;

export const SettingsTab = styled.div`
  display: flex;
  width: 100%;
  max-width: 550px;
  flex: 1 1 100%;
  height: 100%;
  flex-wrap: wrap;
  gap: 15px;
  padding: 15px;
  background-color: var(--medGrey);
  border-radius: 15px;
  box-shadow: 0 4px 8px 5px rgba(0, 0, 0, 0.2),
    0 6px 20px 5px rgba(0, 0, 0, 0.19);

  h2 {
    font-size: 1.6rem;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
  span {
    font-size: 1.6rem;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
  label {
    font-size: 1.6rem;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
`;

export const SettingsTabExtension = styled.div`
  padding: 15px 5px 5px 5px;
  width: 100%;
  display: flex;
  flex: 1 1 100%;
  border-top: 3px solid #00adb5;
  gap: 15px;
  flex-wrap: wrap;
  label {
    font-size: 1.6rem;

    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
  }
  select {
    font-size: 1rem;
  }
`;
