import React, { useRef, useEffect, useState, forwardRef } from "react";
// Routing
import { Link } from "react-router-dom";
// Style
import {
  WrapperSelected,
  WrapperNotSelected,
  UnderlineBar,
  TextIconWrapper,
  TextIconContent,
  IntermediateWrapper,
} from "./NavigationButton.styles";

const NavigationButton = ({ text, icon_name, selected, url, callback }) => {
  // Following code makes sure the underline bar dynamically adjusts to the size of the text

  // This makes sure divs representing buttons have space to grow when the font weight increases on hover
  /*useEffect(() => {
    const ro = new ResizeObserver((entries) => {
      for (let entry of entries) {
        if (minWidth === 0 || entry.target.offsetWidth + 10 < minWidth) {
          setMinWidth(entry.target.offsetWidth + 10);
        }
      }
    });
    ro.observe(textRef.current);

    return () => ro.disconnect();
  }, [minWidth]);
  */
  /*
  useEffect(() => {
    if (minWidth === 0 || textRef.current.offsetWidth + 10 < minWidth) {
      setMinWidth(textRef.current.offsetWidth + 10);
    }
  }, [minWidth]);
  */
  const buttonContent = <ButtonContent text={text} icon_name={icon_name} />;

  return selected ? (
    <Link to={url} style={{ textDecoration: "none" }}>
      <WrapperSelected>{buttonContent}</WrapperSelected>
    </Link>
  ) : (
    <Link to={url} style={{ textDecoration: "none" }}>
      <WrapperNotSelected>{buttonContent}</WrapperNotSelected>
    </Link>
  );
};

// Returns the text/icon and the underline bar
const ButtonContent = forwardRef((props, ref) => (
  <TextIconWrapper>
    <TextIconContent ref={ref}>
      {props.icon_name ? (
        <span class="material-symbols-outlined">{props.icon_name}</span>
      ) : null}
      <h3>{props.text}</h3>
    </TextIconContent>
    <UnderlineBar className="UnderlineBar" />
  </TextIconWrapper>
));

export default NavigationButton;
