import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

export const StepBanner = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--darkGrey);
  padding: 20px;

  h2 {
    margin: 0;
    padding: 0;
  }
`;

export const StepDescription = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 25vw;
  margin-top: 40px;
  margin-bottom: 70px;
  h2 {
    color: black;
    margin-top: 0;
  }
  img {
    width: 100%;
    height: auto;
  }
`;

export const GetStartedButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 70px;
  margin: 20px 0;
  background-color: var(--darkGrey);
  border: 0;
  color: white;
  border-radius: 30px;
  font-size: var(--fontBig);
  transition: all 0.3s;
  outline: none;
  cursor: pointer;
  &:hover {
    opacity: 0.8;
  }
  .material-symbols-outlined {
    scale: 0.7;
  }
`;
