import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
  :root {
    --maxWidth: 1280px;
    --white: #fff;
    --lightGrey: #eee;
    --medGrey: #353535;
    --darkGrey: #1c1c1c;
    --fontSuperBig: 2.5rem;
    --fontBig: 1.5rem;
    --fontMed: 1.2rem;
    --fontSmall: 1rem;
  }

  * {
    box-sizing: border-box;
    font-family:  "Abel",sans-serif;
    accent-color: #00adb5;
  }
  html{

  }
  body {
    margin: 0;
    padding: 0;

    h1 {
      font-size: 4rem;
      font-weight: 600;
      color: var(--white);
    }

    h2 {
      font-size: 2rem;
      font-weight: 600;
      color: var(--white);
    }

    h3 {
      font-size: 1.3rem;
      font-weight: 400;
      margin: 0;
    }
    h4 {
      font-size: 1rem;
      font-weight: 400;
      margin: 0;
    }

    p {
      font-size: 1rem;
      font-weight: 400;
    }
  }
`;

export default GlobalStyle;
