import React from "react";

import {
  Wrapper,
  SettingsTab,
  RadioButtonContainer,
  PaletteDropdown,
  CustomPaletteSelectContainer,
  Palette,
  SelectedPaletteDisplay,
  SelectedPaletteContainer,
  Spinner,
  SpinnerContainer,
} from "./PaletteSettingsPanel.styles";

import palettes from "../../colorPalettes/palettes.json";

const PaletteSettingsPanel = ({
  autoPalette,
  autoPaletteSize,
  autoGeneratedPalettes,
  selectedCustomPalette,
  setAutoPalette,
  setAutoPaletteSize,
  setSelectedCustomPalette,
  loading,
  loadingStage,
}) => {
  return (
    <Wrapper>
      <AutoPalleteTab
        autoPalette={autoPalette}
        setAutoPalette={setAutoPalette}
        autoPaletteSize={autoPaletteSize}
        setAutoPaletteSize={setAutoPaletteSize}
        autoGeneratedPalettes={autoGeneratedPalettes}
        loading={loading}
        loadingStage={loadingStage}
      />
      <CustomPalleteTab
        autoPalette={autoPalette}
        setAutoPalette={setAutoPalette}
        selectedCustomPalette={selectedCustomPalette}
        setSelectedCustomPalette={setSelectedCustomPalette}
      />
    </Wrapper>
  );
};

export default PaletteSettingsPanel;

const AutoPalleteTab = ({
  autoPalette,
  setAutoPalette,
  autoPaletteSize,
  setAutoPaletteSize,
  autoGeneratedPalettes,
  loading,
  loadingStage,
}) => {
  const options = [];
  const onChange = (e) => setAutoPaletteSize(parseInt(e.target.value));

  for (let i = 1; i < 9; i++) {
    options.push(
      <option value={2 ** i} selected={2 ** i == autoPaletteSize} key={2 ** i}>
        {2 ** i}
      </option>
    );
  }

  return (
    <SettingsTab>
      <RadioButton
        isChecked={autoPalette}
        onClick={(e) => setAutoPalette(true)}
      />
      <h2>Auto generate palette</h2>

      <PaletteDropdown show={autoPalette} center={false}>
        <label for="paletteSizeSelect">Palette size:</label>
        <select onChange={onChange} id="paletteSizeSelect">
          {options}
        </select>

        {autoGeneratedPalettes.length == 0 ? (
          loading && (
            <SpinnerContainer>
              <Spinner />
              {loadingStage}
            </SpinnerContainer>
          )
        ) : (
          <SelectedPaletteVisualizer
            palette={autoGeneratedPalettes[Math.log2(autoPaletteSize) - 1]}
          />
        )}
      </PaletteDropdown>
    </SettingsTab>
  );
};

const CustomPalleteTab = ({
  autoPalette,
  setAutoPalette,
  selectedCustomPalette,
  setSelectedCustomPalette,
}) => {
  const mapPaletteListToPalettes = (palette, index) => {
    let divSize = {};
    if (palette["colors"].length <= 16)
      divSize = { width: "60px", height: "30px" };
    else divSize = { width: "25px", height: "25px" };

    return (
      <Palette
        key={index}
        name={palette["slug"]}
        onClick={(e) => setSelectedCustomPalette(index)}
        selected={selectedCustomPalette == index}
      >
        {palette["colors"].map(mapColorArrayToDivs(divSize))}
      </Palette>
    );
  };

  return (
    <SettingsTab>
      <RadioButton
        isChecked={!autoPalette}
        onClick={(e) => setAutoPalette(false)}
      />
      <h2>Select custom palette</h2>

      <PaletteDropdown show={!autoPalette} center={true}>
        <CustomPaletteSelectContainer>
          {palettes.map(mapPaletteListToPalettes)}
        </CustomPaletteSelectContainer>

        <SelectedPaletteVisualizer
          palette={palettes[selectedCustomPalette]["colors"]}
        />

        <a
          href={`https://lospec.com/palette-list/${palettes[selectedCustomPalette]["slug"]}`}
          style={{ color: "rgba(255,255,255,0.6)" }}
          target="_blank"
        >{`Credit: ${palettes[selectedCustomPalette]["slug"]}`}</a>
      </PaletteDropdown>
    </SettingsTab>
  );
};

const SelectedPaletteVisualizer = ({ palette }) => {
  let colorDivSize = {};

  if (palette.length <= 32) {
    colorDivSize = { minWidth: "100px", maxWidth: "50%", height: "50px" };
  } else if (palette.length > 32 && palette.length <= 64) {
    colorDivSize = { width: "50px", height: "50px" };
  } else if (palette.length > 64) {
    colorDivSize = { width: "25px", height: "25px" };
  }
  return (
    <SelectedPaletteContainer>
      <h2>Selected palette:</h2>

      <SelectedPaletteDisplay>
        {palette.map(
          mapColorArrayToDivs({
            flex: "1 1 auto",
            ...colorDivSize,
          })
        )}
      </SelectedPaletteDisplay>
    </SelectedPaletteContainer>
  );
};
const mapColorArrayToDivs = (style) => (color) =>
  <div style={{ backgroundColor: color, ...style }}></div>;

const RadioButton = ({ isChecked, onClick }) => (
  <RadioButtonContainer is_checked={isChecked}>
    <input type="radio" checked={isChecked} onClick={onClick} />
    <span />
  </RadioButtonContainer>
);
